<template>
  <div>
    <main>
      <div class="title">
        <h1>
          <BarLoader style="margin: 0 auto; border-radius:5px" v-if="artworkLoading"></BarLoader>
          <StateDisplay :state="getArtwork.state"/>
          {{getArtwork.name}}
          <span v-if="getArtwork.date" class="date">({{getArtwork.date}})</span>
        </h1>
        <span class="actions" v-if="jwt && level>=2">
          <img src="@/assets/icon/edit.svg" class="action" @click="$router.push('/'+getPerson.slug+'/'+getArtwork.slug+'/edit')">
        </span>
      </div>
      <input v-if="!artworkLoading" class="print" type="button" value="Imprimer cette fiche" @click="$router.push('/'+getPerson.slug+'/'+getArtwork.slug+'/print')" />
      <div class="card content" v-if="artworkLoading">
        <BarLoader style="margin: 0 auto; border-radius:5px"></BarLoader>
      </div>
      <div class="card content" v-else>
        <viewer :value="getArtwork.content" v-if="getArtwork.content && getArtwork.content !=''"/>
        <viewer value="La fiche n'est pas encore remplie" v-else/>
        <Date :element="getArtwork"/>
      </div>
      <div class="card text" v-if="getArtwork.text">
        <h2>Texte</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="artworkLoading"></BarLoader>
        <viewer :value="getArtwork.text" />
      </div>
      <div class="card notes" v-if="getArtwork.notes">
        <h2>Notes</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="artworkLoading"></BarLoader>
        <viewer :value="getArtwork.notes" />
      </div>
    </main>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import Viewer from '@toast-ui/vue-editor/src/Viewer.vue'
import StateDisplay from '@/components/state/Display.vue'
import Date from '@/components/date.vue'


export default {
  name: 'home',
  components: {
    Viewer,
    StateDisplay,
    Date
  },
  data () {
    return {
        editable: false,
        person: {}
    }
  },
  created () {
    window.scrollTo(0, 0)
    this.fetchArtworks().then(()=>{
        if(this.$route.params.person) {
            if(this.getPersons.length!==0) {
                this.person = {...this.getPersons.filter(person=>person.slug==this.$route.params.person)[0]}
                let artworks = this.getArtworks.filter(artwork => artwork.person == this.person.name && artwork.slug == this.$route.params.artwork)
                if (artworks.length ==0) {
                    this.$router.push('/error/404')
                } else {
                    this.setArtwork(artworks[0])
                }
            } else {
                this.fetchPerson(this.$route.params.person).then(()=>{
                    let artworks = this.getArtworks.filter(artwork => artwork.person == this.getPerson.name && artwork.slug == this.$route.params.artwork)
                    if (artworks.length ==0) {
                      this.$router.push('/error/404')
                    } else {
                      this.setArtwork(artworks[0])
                    }
                })
            }
        } else if (this.$route.params.artwork) {
          let artworks = this.getArtworks.filter(artwork => artwork.slug == this.$route.params.artwork)
          if (artworks.length ==0) {
            this.$router.push('/error/404')
          } else {
            this.setArtwork(artworks[0])
          }
        }
      
        
    })
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'level',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('artworks',[
        'getArtwork',
        'getArtworks',
        'artworkLoading'
        ]),
        ...mapGetters('persons',[
          'setPerson',
          'getPerson',
          'getPersons'
        ])
  },
  methods: {
    ...mapActions('artworks',[
      'setArtwork',
      'fetchArtworks'
    ]),
    ...mapActions('persons',[
      'fetchPerson'
    ])
  }
}
</script>
