<template>
    <sub class="updated_at">{{getDate}}{{getEditor}}</sub>
</template>
<script>
export default {
    props: ['element'],
    computed: {
        getDate: function() {
            if(this.element.updated_at !== null) {
                var d = new Date(Date.parse(this.element.updated_at));
                return "Modifié le "+d.toLocaleDateString('fr-FR', { weekday:'long', year:'numeric', month:'long', day:'numeric', hour: 'numeric', minute:'numeric' });
            } else if(this.element.created_at !== null) {
                var d = new Date(Date.parse(this.element.created_at));
                return "Créé le "+d.toLocaleDateString('fr-FR', { weekday:'long', year:'numeric', month:'long', day:'numeric', hour: 'numeric', minute:'numeric' });
            } else {
                return ''
            }
           
        },
        getEditor: function() {
            if(this.element.editor !== null) return ` par ${this.element.editor}`
        }
    }
}
</script>